import React from "react";
import { Route, Routes } from "react-router-dom";

const Variables = React.lazy(() => import("../variables/variables"));
const RateLimits = React.lazy(() => import("../api/ratelimits"));
const Modules = React.lazy(() => import("../modules/modules"));
const Module = React.lazy(() => import("../modules/module"));
const Debugging = React.lazy(() => import("../debugging/debugging"));
const Debugger = React.lazy(() => import("../debugging/debugger"));
const Templates = React.lazy(() => import("../template/templates"));

const PublishedFolders = React.lazy(
  () => import("../dashboard/folders")
);

export default function PlatformRoute() {
  return (
    <Routes>
      <Route path="folders" element={<PublishedFolders />} />
      <Route path="variables" element={<Variables />} />
      <Route path="ratelimits" element={<RateLimits />} />
      <Route path="module/:id" element={<Module />} />
      <Route path="modules" element={<Modules />} />
      <Route path="debugging" element={<Debugging />} />
      <Route path="debugging/:processId/:runspaceId" element={<Debugger />} />
      <Route path="templates" element={<Templates />} />
    </Routes>
  );
}
